import styles from "./SignUp.module.css";
import { useNavigate } from "react-router-dom";
import { useState } from "react";
import axios from "axios";

const SignUp = () => {
  const navigate = useNavigate();
  const [id, setId] = useState("");
  const [pw, setPw] = useState("");
  const [responseData, setResponseData] = useState(null);

  // ,  4   
  const wordCondition = () => {
    if (id.length < 4 || pw.length < 4) {
      alert("4  !");
      return false;
    }
    return true;
  };

  const handlePostRequest = () => {
    const url = "/api/users/sign-up";
    const data = {
      username: id,
      password: pw,
    };

    axios
      .post(url, data)
      .then((response) => {
        console.log(" :", response.data); //   
        setResponseData(response.data);
        //      
        alert(" !  ");
        navigate("/");
      })
      .catch((error) => {
        console.error(" :", error);
      });
  };

  return (
    <div className={styles.SignUp}>
      <h2 className={styles.title}></h2>
      <div className={styles.input_box}>
        <div className={styles.id_box}>
          <h4></h4>
          <input
            className={styles.ID}
            type="email"
            placeholder="4  "
            value={id} // id  
            onChange={(e) => setId(e.target.value)} // id    
          />
        </div>
        <div className={styles.pw_box}>
          <h4></h4>
          <input
            className={styles.PW}
            type="password"
            placeholder="4  "
            value={pw} // pw  
            onChange={(e) => setPw(e.target.value)} // pw    
          />
        </div>
        <button onClick={() => wordCondition() && handlePostRequest()}>
          
        </button>
      </div>
    </div>
  );
};

export default SignUp;
