import Emoji from "./Emoji";
import styles from "./TodoItem.module.css";
import { useState, useRef } from "react";

const TodoItem = ({
  _id,
  content,
  is_checked,
  date,
  emoji,
  onCheck,
  onDelete,
  onEdit,
  onReview,
}) => {
  const onChangeCheckbox = () => {
    onCheck(_id);
  };

  const onClickDelete = () => {
    onDelete(_id);
  };

  let style1 = { color: "black" };
  let style2 = { color: "gray", textDecorationLine: "line-through" };

  const [isEdit, setIsEdit] = useState(false); // isEdit ?  :  X
  const [feel, setFeel] = useState(false);
  const [emojiSelection, setEmojiSelection] = useState(emoji);

  const onSelectEmoji = (selectedEmoji) => {
    setEmojiSelection(selectedEmoji); //    
    onReview(_id, selectedEmoji);
    setFeel(false); //   
  };

  const toggleIsEdit = () => setIsEdit(!isEdit);

  //    
  //   ->    content   
  const [localContent, setLocalContent] = useState(content);

  const localContentInput = useRef();

  //       
  //  ->  ->  :  content   ()
  const handleQuitEdit = () => {
    setIsEdit(false);
    setLocalContent(content); //        
  };

  //       
  const handleEdit = () => {
    //    1   focus
    if (localContent.length < 1) {
      localContentInput.current.focus();
      return;
    }
    onEdit(_id, localContent); // _id(), localContent(  ) onEdit   
    toggleIsEdit(); // isEdit true false   .
  };

  // enter    
  const onKeyDown = (e) => {
    if (e.keyCode === 13) {
      handleEdit();
    }
  };

  return (
    <div className={styles.TodoItem}>
      <div className={styles.checkbox_col}>
        <input
          onChange={onChangeCheckbox}
          checked={is_checked}
          type="checkbox"
        />
      </div>
      <div className={styles.title_col}>
        <span style={is_checked ? style2 : style1}>
          {/* isEdit ?   :  content */}
          {isEdit ? (
            <>
              <textarea
                ref={localContentInput}
                value={localContent}
                onChange={(e) => {
                  setLocalContent(e.target.value);
                }}
                onKeyDown={onKeyDown}
              />
            </>
          ) : (
            <>{content}</>
          )}
        </span>
      </div>

      <div className={styles.btn_col}>
        {isEdit ? (
          <>
            {/*   (isEdit === true)  */}
            <button onClick={handleQuitEdit}></button>
            <button onClick={handleEdit}></button>
          </>
        ) : (
          <>
            {/*    */}
            <button onClick={() => setFeel(!feel)}>
              {emojiSelection === "" ? "" : emojiSelection}
              {feel && <Emoji onSelectEmoji={onSelectEmoji} />}
            </button>
            <button onClick={toggleIsEdit}></button>
            <button onClick={onClickDelete}></button>
          </>
        )}
      </div>
    </div>
  );
};
export default TodoItem;
