import React, { useState } from "react";
import loginImg from "../img/lion.jpeg";
import { useNavigate } from "react-router-dom";
import styles from "./Login.module.css";
import axios from "axios";

export default function Login() {
  let navigate = useNavigate();
  const [id, setId] = useState("");
  const [pw, setPw] = useState("");

  const goHome = () => {
    navigate("/Home");
  };

  const goSignUp = () => {
    navigate("/SignUp");
  };

  // ,  4   
  const wordCondition = () => {
    if (id.length < 4 || pw.length < 4) {
      alert("4  !");
      return false;
    }
    return true;
  };

  //   Enter   
  // Enter     
  const handleOnKeyPress = (e) => {
    if (e.key === "Enter") {
      if (wordCondition()) {
        handleLogin();
      }
    }
  };

  const handleLogin = () => {
    if (!wordCondition()) {
      return; // (4 )    
    }

    const url = `/api/users/log-in`;
    const requestData = {
      username: id,
      password: pw,
    };

    axios
      .post(url, requestData)
      .then((response) => {
        localStorage.setItem("userId", response.data.userId); // userId localStorage   
        goHome();
      })
      .catch((error) => {
        if (error.response && error.response.status === 401) {
          // 401   
          alert(" .    .");
        } else {
          //   
          console.error(" :", error);
        }
      });
  };

  return (
    <div className={styles.Login}>
      <div className={styles.loginPage}>
        <img src={loginImg} alt="" width="500px" />
        <div className={styles.loginBox}>
          <div className={styles.phrase}>
            <p></p>
            <p></p>
          </div>
          <div className={styles.login}>
            <input
              className={styles.ID}
              type="email"
              placeholder=" "
              value={id} // id  
              onChange={(e) => setId(e.target.value)} // id    
            />
            <br />
            <input
              className={styles.PW}
              type="password"
              placeholder=" "
              onKeyPress={handleOnKeyPress}
              value={pw} // pw  
              onChange={(e) => setPw(e.target.value)} // pw    
            />
            <br />
            <button onClick={handleLogin}></button>
            <p className={styles.sign_up} onClick={goSignUp}>
                ?
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
