import React, { useState, useEffect } from "react";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import styles from "./Home.module.css";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const Home = () => {
  const navigate = useNavigate();
  const [selectedDate, setSelectedDate] = useState(new Date());

  const handleDateChange = (date) => {
    const isoDateString = date.toISOString(); // ISO 8601   .
    date.setDate(date.getDate() + 1); //    .
    const formattedDate = date.toISOString().split("T")[0]; // , ,   .

    const month = date.getMonth() + 1; // 0  +1
    const day = date.getDate() - 1;
    localStorage.setItem("month", String(month));
    localStorage.setItem("day", String(day));
    setSelectedDate(date);
    navigate(`/todoPage/${formattedDate}`);
  };

  const handleLogout = () => {
    const userId = localStorage.getItem("userId");

    axios
      .get(`/api/users/log-out`)
      .then((response) => {
        console.log(response.data);
        navigate("/");
      })
      .catch((error) => {
        console.error(" :", error);
      });
  };

  return (
    <div className={styles.Home}>
      <h2 className={styles.title}></h2>
      <Calendar
        onChange={handleDateChange}
        value={selectedDate}
        className={styles.calendar}
        locale="en-US" //   
      />
      <button className={styles.log_out} onClick={handleLogout}>
        
      </button>
    </div>
  );
};

export default Home;
